
export default function preventSleep(){
	const videoWrap = document.createElement('div');
	// video code from https://www.npmjs.com/package/nosleep.js
	videoWrap.innerHTML = `<video class="prevent-sleep" style="opacity: 0; pointer-events: none; position: absolute; height: 0; width: 0" tabindex="-1" muted="" title="Prevent web browser from going to sleep" playsinline="" loop=""><source src="data:video/webm;base64,GkXfo0AgQoaBAUL3gQFC8oEEQvOBCEKCQAR3ZWJtQoeBAkKFgQIYU4BnQI0VSalmQCgq17FAAw9CQE2AQAZ3aGFtbXlXQUAGd2hhbW15RIlACECPQAAAAAAAFlSua0AxrkAu14EBY8WBAZyBACK1nEADdW5khkAFVl9WUDglhohAA1ZQOIOBAeBABrCBCLqBCB9DtnVAIueBAKNAHIEAAIAwAQCdASoIAAgAAUAmJaQAA3AA/vz0AAA=" type="video/webm"><source src="data:video/mp4;base64," type="video/mp4"></video>`;
	const video = videoWrap.querySelector('video');
	document.querySelector('body').appendChild(video);
	video.play();
}
